:root {
    --leaflet-popup-background: #fff;
    --leaflet-popup-text: inherit;
}

/** Theming of the leaflet stuff **/
.leaflet-popup-content-wrapper,
.leaflet-popup-tip,
.leaflet-control-layers-expanded,
.leaflet-control-layers,
.leaflet-bar a {
    background-color: var(--leaflet-popup-background) !important;
    color: var(--leaflet-popup-text) !important;
}

.leaflet-bar a {
    &:hover {
        background: var(--leaflet-popup-background);
    }
}

.leaflet-popup-content-wrapper, .leaflet-popup-tip {
    background: var(--leaflet-popup-background) !important;

    .leaflet-popup-content {
        color: var(--leaflet-popup-text);
    }
}

.leaflet-container {
    background: transparent !important;
}

/** Leaflet marker **/
.leaflet-ruler {
    height: 48px;
    width: 48px;
    background-image: url("/images/vendor/leaflet/ruler/icon.png"); /* <div>Icons made by <a href="http://www.freepik.com" title="Freepik">Freepik</a> from <a href="http://www.flaticon.com" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0" target="_blank">CC 3.0 BY</a></div> */
    background-repeat: no-repeat;
    background-position: center;
}

.leaflet-ruler:hover {
    background-image: url("/images/vendor/leaflet/ruler/icon-colored.png"); /* <div>Icons made by <a href="http://www.freepik.com" title="Freepik">Freepik</a> from <a href="http://www.flaticon.com" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0" target="_blank">CC 3.0 BY</a></div> */
}

.leaflet-ruler-clicked {
    height: 48px;
    width: 48px;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("/images/vendor/leaflet/ruler/icon-colored.png");
}

.leaflet-bar {
    background-color: #ffffff;
}

.result-tooltip {
    background-color: white;
    border-width: medium;
    border-color: var(--map-ruler-color);
    font-size: smaller;
}

.moving-tooltip {
    background-color: rgba(255, 255, 255, .7);
    background-clip: padding-box;
    opacity: 0.5;
    border: dotted;
    border-color: var(--map-ruler-color);;
    font-size: smaller;
}

.plus-length {
    padding-left: 45px;
}

.marker-preset-list .preset {
    padding: 0.8rem;
    border: 1px solid var(--tab-background);
    background-color: var(--tab-background);
    border-radius: 5px;
    &:hover {
        border: 1px solid var(--tab-hover-background);
    }

    .preset-use {
        cursor: pointer;
        &:hover {
            text-decoration: underline;
        }
    }
}

@import "~leaflet.markercluster/dist/MarkerCluster.css";
@import "~leaflet.markercluster/dist/MarkerCluster.Default.css";
